img {
  display: block;
  width: 100%;
  height: auto;
}

.container {
  width: 375px;
  margin: 0 auto;
  padding: 24px 25px 30px 24px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
